@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;600&display=swap);
.ToggleButtons{font-family:"Roboto Mono", monospace;display:flex;align-items:center}.ToggleButtons__option{font-size:0.875rem;background:var(--background-primary);box-shadow:var(--box-shadow-primary);color:rgba(0,0,0,0.38);border:1px solid rgba(0,0,0,0.12);padding:0.5rem 0.125rem;width:101px;font-weight:600;transition:all 200ms;cursor:pointer}.ToggleButtons__option:hover{background:rgba(0,0,0,0.07)}.ToggleButtons__option:first-of-type{border-bottom-left-radius:4px;border-top-left-radius:4px}.ToggleButtons__option:last-of-type{border-bottom-right-radius:4px;border-top-right-radius:4px}.ToggleButtons__option_selected{opacity:1;border-color:transparent;background:rgba(0,0,0,0.12);color:rgba(0,0,0,0.54);box-shadow:rgba(50,50,93,0.1) 0px 30px 60px -12px inset,rgba(0,0,0,0.1) 0px 18px 36px -18px inset}.ToggleButtons__option_selected:hover{background:rgba(0,0,0,0.12)}

.Memory{background:#e6f0ff;padding:15px 20px 20px 20px;overflow:visible;transition:all 100ms}.Memory__memoryContainer{max-width:582px;max-height:572px;overflow:scroll}.Memory__row,.Memory__column{fill:transparent}.Memory__row_preselected,.Memory__column_preselected{stroke:var(--cta);stroke-width:2px;opacity:0.75;stroke-dasharray:20 25}.Memory__row_selected,.Memory__column_selected{stroke:var(--cta);stroke-width:2px;opacity:1}.Memory__header{fill:var(--text-primary);text-anchor:end;dominant-baseline:middle}.Memory__cell,.Memory__cellAddress{fill:#bbbbbb;transition:0.35s}.Memory__cell_dirty,.Memory__cellAddress_dirty{fill:#99befa}.Memory__cell_preselected,.Memory__cellAddress_preselected{fill:var(--cta);opacity:0.7}.Memory__cell_selected,.Memory__cellAddress_selected{fill:var(--cta)}.Memory__addressLabel{font-size:0.875rem}.Memory__addressLabel,.Memory__dataLabel{font-family:"Roboto Mono", monospace;fill:white;text-anchor:end;dominant-baseline:middle}.Memory__viewOptions{padding-bottom:1rem}

.RamVisualization{padding:2rem 6.25rem}.RamVisualization__caption{display:flex}.RamVisualization__header{color:var(--text-primary);margin-bottom:2.25rem;margin-right:1rem}.RamVisualization__visualization{display:flex}.RamVisualization__visualizationSettings{min-width:15rem}.RamVisualization__memory{display:flex}.RamVisualization__notes{margin-top:1.25rem;font-size:0.75rem;color:var(--text-primary);opacity:0.7}

@-webkit-keyframes emphasise{0%{font-weight:600}100%{font-weight:400}}@keyframes emphasise{0%{font-weight:600}100%{font-weight:400}}.Pin__pinSignificance{fill:var(--text-primary);opacity:0.7;font-size:0.675rem}.Pin__pinLabel{font-family:"Roboto Mono", monospace;fill:var(--cta)}.Pin__pinLabel_selected{-webkit-animation:emphasise 0.15s forwards;animation:emphasise 0.15s forwards;animation-direction:reverse}.Pin__pinLabel_disabled{fill:var(--text-primary)}.Pin__pinArea{cursor:pointer;fill:transparent}.Pin__pinArea_disabled{cursor:default}.Pin__pinLine{stroke:var(--text-gray);stroke-linecap:round}.Pin__pinLine_long{stroke-dasharray:100 100;transition:1s ease-in}.Pin__pinLine_short{stroke-dasharray:25 100;transition:1s ease-in}

@-webkit-keyframes hideshow{0%{stroke-dashoffset:-100}100%{stroke-dashoffset:10}}@keyframes hideshow{0%{stroke-dashoffset:-100}100%{stroke-dashoffset:10}}.CrossPinsNotation__pinsDivider{stroke:var(--text-gray);stroke-linecap:round}.CrossPinsNotation__pinsDivider_visible{transition-delay:1s}.CrossPinsNotation__pinsDivider_invisible{opacity:0}.CrossPinsNotation__pinsNumber{fill:var(--text-primary);font-weight:600;opacity:0.4}.CrossPinsNotation__pinsNumber_visible{transition-delay:1s}.CrossPinsNotation__pinsNumber_invisible{opacity:0}.CrossPinsNotation__crossNotation{opacity:0;stroke-width:1px;stroke-linecap:round}.CrossPinsNotation__crossNotation_visible{opacity:1;transform-origin:center;transform-box:fill-box;transform:rotate(-45deg);transition:transform 1s, opacity 0s;transition-delay:1s}.CrossPinsNotation__pinsNotation{stroke-width:3;opacity:1;stroke-dasharray:100;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.CrossPinsNotation__pinsNotation_visible{stroke-dashoffset:10}.CrossPinsNotation__pinsNotation_visibleAnimated{-webkit-animation:hideshow 1s ease-in 1;animation:hideshow 1s ease-in 1}.CrossPinsNotation__crossNotation,.CrossPinsNotation__pinsNotation{stroke:var(--text-gray)}

.PinsBlock{padding:8px 0 0 0;display:flex;height:-webkit-max-content;height:-moz-max-content;height:max-content}.PinsBlock__length{padding-right:16px;width:190px}.PinsBlock__list{margin-left:auto}

.MemoryControls__tacts{display:block;margin:1rem 0}.MemoryControls__tactsNumber{width:2rem;margin-right:0.5rem}.MemoryControls__bitsWidth{display:block;color:var(--text-primary);font-weight:300;opacity:0.8}.MemoryControls__clockPinBlock{height:120px;opacity:1;transition:height 700ms, opacity 700ms}.MemoryControls__clockPinBlock_hidden{height:0;opacity:0;transition:height 700ms, opacity 500ms}.MemoryControls__enabledBlock,.MemoryControls__readWriteBlock,.MemoryControls__clockPinBlock,.MemoryControls__addressBlock,.MemoryControls__dataBlock{display:flex;justify-content:space-between;background:#cfcfcf24;padding:0.25rem 0 0.25rem 3rem}.MemoryControls__enabledBlock,.MemoryControls__readWriteBlock,.MemoryControls__clockPinBlock,.MemoryControls__addressBlock{margin-top:0.5rem}.MemoryControls__enabledLabel,.MemoryControls__readWriteLabel,.MemoryControls__clockLabel,.MemoryControls__addressLabel,.MemoryControls__dataLabel{width:10rem;margin-right:1.5rem;padding:9px 0 8px 0;line-height:20px;color:var(--text-primary)}.MemoryControls__enabledLabel,.MemoryControls__read,.MemoryControls__write{cursor:pointer}.MemoryControls__enabledLabel_enabled,.MemoryControls__read_enabled,.MemoryControls__write_enabled{font-weight:bold}.MemoryControls__dataMode{display:flex}.MemoryControls__currentTacts{opacity:0.7;font-size:0.875rem}.MemoryControls__tactsNote{opacity:0.7;font-size:0.75rem;white-space:nowrap}.MemoryControls__ras,.MemoryControls__cas{position:relative}.MemoryControls__rasLabel,.MemoryControls__casLabel{position:absolute;top:12px;left:20px;margin-right:0.5rem;margin-bottom:10px;font-size:0.75rem;color:var(--text-primary);text-transform:uppercase}.MemoryControls__rasLabel_active,.MemoryControls__casLabel_active{border-top:1px solid var(--text-primary);font-weight:bold}.MemoryControls__addressPins{width:160px}.MemoryControls__viewOptions{display:block;padding:15px 0 1rem 0}

.VisualizationSettings{display:flex}.VisualizationSettings__pinNotationInput,.VisualizationSettings__tactingInput,.VisualizationSettings__rasCasInput{height:1.5rem;width:1.5rem;margin-left:1.5rem;margin-right:0.125rem}.VisualizationSettings__pinNotation,.VisualizationSettings__tacting,.VisualizationSettings__rasCas{color:var(--text-primary);margin-bottom:0.25rem;display:flex;align-items:center}

input[type="range"]{-webkit-appearance:none;margin:18px 0;width:100%}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-runnable-track{background:#505e6b5e;background:var(--text-secondary);height:2px;border-radius:2px}input[type="range"]::-moz-range-track{width:100%;height:2px;cursor:pointer;background:#505e6b5e;background:var(--text-secondary);border-radius:2px;border:none}input[type="range"]::-webkit-slider-thumb{box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d;border:1px solid #515e6b;border:1px solid var(--text-primary);height:14px;width:14px;border-radius:100%;-webkit-appearance:none;background:#ffffff;background:var(--background-primary);margin-top:-5px;cursor:pointer}input[type="range"]::-moz-range-thumb{box-shadow:1px 1px 1px #515e6b,0px 0px 1px #0d0d0d;box-shadow:1px 1px 1px var(--text-primary),0px 0px 1px #0d0d0d;border:1px solid #515e6b;border:1px solid var(--text-primary);height:12px;width:12px;border-radius:100%;background:#ffffff;background:var(--background-primary);cursor:pointer}:root{--black: black;--cta: #488dff;--background-primary: #ffffff;--text-primary: #515e6b;--text-secondary: #505e6b5e;--text-gray: #9fa5aa;--box-shadow-primary: rgba(100, 100, 111, 0.3) 0px 7px 29px 0px}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

